.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.bg {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  overflow: scroll;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url("https://i.imgur.com/ETGUWxH.png")
}

.kaffee {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(18px + 2vmin);
  color: rgb(238, 238, 238);
  font-kerning: 20px;
  text-shadow: 1px 1px rgb(4, 4, 4);
  font-family: 'Hind', sans-serif;
}
